.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 0.99s linear; */
  cursor: pointer;
  height: 200px;
}

.App-header {
  background-color: #02123a;
  height: 200px;
  padding: 30px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

/* @keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
} */

.icon {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
  -webkit-transition: 400ms -webkit-transform;
  transition: 400ms -webkit-transform;
  -o-transition: 400ms transform;
  transition: 400ms transform;
  transition: 400ms transform, 400ms -webkit-transform;
}

.icon.selected {
  -webkit-animation: spin 400ms;
          animation: spin 400ms;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

@-webkit-keyframes spin {
  0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  
  100% {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
  }
}

@keyframes spin {
  0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
  }
  
  100% {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
  }
}
.Quote {
  font-size: 5vw;
  font-family: initial;
  color: #53607d;
  padding-top: 120px;
  padding-left: 50px;
  padding-right: 50px;
}



